import { Button, Card, Col, Form, Row } from "@themesberg/react-bootstrap"
import React, { useState } from "react"
import { TnosDataTable } from "../../components/TnosDataTable"
import axios from "axios"
import Swal from "sweetalert2";
import { logDOM } from "@testing-library/react";

export default () => {

    const initialData = {
        tnos_service_id: "3",
        tnos_subservice_id: "9",
        order_total: 0,
        needs: "",
        user_id: "",
        name: "",
        email: "",
        phone: "",
        file_document: "",
        ketentuan_cek: false,
    }

    const [formData, setFormData] = useState(initialData)

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    }

    const handleSubmit = async (e) => {
        e.preventDefault()

        try {
            const response = await axios.post(
                `${process.env.REACT_APP_API_PWA_TNOSWORLD_URL}/manual-order/in-order-manual`,
                formData
            )
            
            if (response.data.status === true) {
                setFormData(initialData)
                Swal.fire(
                    'Berhasil!',
                    `${response.data.message}`,
                    'success'
                );
            }
            
        } catch (error) {
            console.error("Gagal mengirim data:", error);
        }
    }

    return (
        <>
            <Card border="light" className="bg-white shadow-sm mb-4">
                <Card.Body>
                    <h5 className="mb-4">Tambah Manual Order</h5>
                    <Form onSubmit={handleSubmit}>
                        <Row className="mb-3">
                            <Col md={4}>
                                <Form.Label>Kode Member</Form.Label>
                                <Form.Control
                                    className={`border`}
                                    required
                                    type="text"
                                    name="user_id"
                                    placeholder="Masukkan Kode Member"
                                    value={formData.user_id}
                                    onChange={handleChange}
                                />
                            </Col>
                            <Col md={4}>
                                <Form.Label>Nama</Form.Label>
                                <Form.Control
                                    className={`border`}
                                    required
                                    type="text"
                                    name="name"
                                    placeholder="Masukkan Nama"
                                    value={formData.name}
                                    onChange={handleChange}
                                />
                            </Col>
                            <Col md={4}>
                                <Form.Label>Biaya</Form.Label>
                                <Form.Control
                                    className={`border`}
                                    required
                                    type="text"
                                    name="order_total"
                                    placeholder="Masukkan Biaya"
                                    value={formData.order_total}
                                    onChange={handleChange}
                                />
                            </Col>
                        </Row>

                        <Row className="mb-3">
                            <Col md={6}>
                                <Form.Label>Email</Form.Label>
                                <Form.Control
                                    className={`border`}
                                    required
                                    type="text"
                                    name="email"
                                    placeholder="Masukkan Email"
                                    value={formData.email}
                                    onChange={handleChange}
                                />
                            </Col>
                            <Col md={6}>
                                <Form.Label>Nomor Handphone</Form.Label>
                                <Form.Control
                                    className={`border`}
                                    required
                                    type="text"
                                    name="phone"
                                    placeholder="Masukkan Nomor Handphone"
                                    value={formData.phone}
                                    onChange={handleChange}
                                />
                            </Col>
                        </Row>

                        <Form.Label>Keperluan</Form.Label>
                        <Form.Control
                            className={`border`}
                            required
                            type="text"
                            name="needs"
                            placeholder="Masukkan Keperluan"
                            as="textarea"
                            rows={3}
                            value={formData.needs}
                            onChange={handleChange}
                        />

                        <div className="mt-3">
                            <Button variant="primary" type="submit">
                                Simpan
                            </Button>
                        </div>
                    </Form>
                </Card.Body>
            </Card>
        </>
    )

}